  border-radius:5px;
  margin: 5px;
  align-items:center;
  height:4vh;
  background-color:dodgerblue;
  font-size:15px;
  cursor: pointer;
}
.login__container {
  width: 400px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  border: 1px solid white;
  padding: 20px;
}
